*{
  margin: 0;
  padding: 0;
  font-size: 20px;
  scroll-behavior: smooth;
  scroll-padding-top: 80px;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.App{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}
h1{
  text-align: center;
  font-size: 40px;
}
div{
  text-align:left;
}
:root{
  --fondo: #ffffff;
}
.Lcontainer{
  flex-grow: 1;
  min-height: 80vh;
  width: 100%;
  background-color: #ffffff;
  background-color: var(--fondo);
  place-content: center;
  display: grid;
  overflow: hidden;
}
.loader {
  font-size: 10px;
  text-indent: -9999em;
  width: 11em;
  height: 11em;
  border-radius: 50%;
  background: linear-gradient(to right, #000 10%, rgba(255, 255, 255, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
          animation: load3 1.4s infinite linear;
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #000;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: '';
}
.loader:after {
  background-color: #ffffff;
  background-color: var(--fondo);
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: '';
  margin: auto;
  position: absolute;
  inset: 0 0 0 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
